.nav__menu-btn {
  cursor: pointer;
  z-index: 3;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 0;
  padding: 0;
  display: inline-flex;
}

.nav__menu-btn .nav__icon--close {
  display: none;
}

.nav__menu-btn.is-open .nav__icon--close {
  display: block;
}

.nav__menu-btn.is-open .nav__icon--menu {
  display: none;
}

@media screen and (min-width: 768px) {
  .nav__menu-btn {
    display: none;
  }
}

.nav__menu-icon {
  width: 40px;
  height: 40px;
}

.nav__icon--menu {
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.nav__icon--menu:hover, .nav__icon--menu:focus {
  fill: #28599e;
}

.nav__icon--close {
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.nav__icon--close:hover, .nav__icon--close:focus {
  fill: #28599e;
}

.nav__container {
  z-index: 2;
  font-size: 20px;
}

@media screen and (min-width: 320px) and (max-width: 767.9px) {
  .nav__container {
    width: 100%;
    height: 100%;
    opacity: 0;
    pointer-events: none;
    background-color: #f0f8ff;
    flex-direction: column;
    justify-content: space-between;
    padding: 48px 40px;
    display: flex;
    position: fixed;
    top: 0;
    right: 0;
    transform: translateX(100%);
    box-shadow: 0 4px 8px rgba(0, 0, 0, .04), 0 2px 4px rgba(0, 0, 0, .08), 0 1px 3px rgba(0, 0, 0, .16);
  }

  .nav__container.is-open {
    opacity: 1;
    pointer-events: auto;
    transform: translateX(0);
  }

  .nav__container.is-closing {
    opacity: 0;
    pointer-events: none;
    transform: translateX(-100%);
  }
}

.no-scroll {
  height: 100%;
  width: 100%;
